import request from "axios";

export const loadNotionContent = (type, id) => {
  const apiCompletionPromise = request({
    method: "GET",
    url: `https://notion-api.splitbee.io/v1/${type}/${id}`,
  });
  return apiCompletionPromise;
};

const NOTION_API = "https://www.notion.so/api/v3";

const loadPageChunkBody = {
  limit: 100,
  cursor: { stack: [] },
  chunkNumber: 0,
  verticalColumns: false,
};

const fetchNotionData = async ({ resource, body, notionToken }) => {
  const res = await fetch(`${NOTION_API}/${resource}`, {
    method: "POST",
    headers: {
      "content-type": "application/json",
      ...(notionToken && { cookie: `token_v2=${notionToken}` }),
    },
    body: JSON.stringify(body),
  }).then((val) => {
    console.log(val);
  });
  return res.json();
};

export const fetchPageById = async (pageId, notionToken) => {
  const res = await fetchNotionData({
    resource: "loadPageChunk",
    body: {
      page: pageId,
      ...loadPageChunkBody,
    },
    notionToken,
  });
  return res;
};
